import React, { useRef, useState, useEffect, useContext } from 'react'
import Editor from "@monaco-editor/react";
import FileInput from './FileInput';
import { options } from './Options';
import LoadingSpinner from './Loader';
import { LoaderContext } from '../contexts/LoaderContext';
import { languageInfo } from '../LanguageInfo';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const CodeEditor = () => {
  const editorRef = useRef();
  const editorRef1 = useRef();
  const { loading, setLoading } = useContext(LoaderContext);
  // const { getAlllanguage } = useLanguageFetch()
  // const [languageID, setLanguageID] = useState("Java");
  const [language, setLanguage] = useState("java");
  const [compilerLang, setCompilerlang] = useState("java");
  const [fileContent, setFileContent] = useState('');
  const [option, setOption] = useState(options);
  const [isButtonDisabled, setButtonDisabled] = useState(false);
  const [compilerOutput, setCompilerOutput] = useState("Output:- ");
  const [error, setError] = useState(false);
  const handleFileRead = (content) => {
    setLoading(true);
    setFileContent(content);
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  };
  const handleEditorDidMount = (editor, monaco) => {
    try {
      editorRef.current = editor;
    } catch (err) {
      console.log(err);
    }
  }

  const handleEditorDidMountOption = (editor) => {
    try {
      editorRef1.current = editor;
    } catch (err) {
      console.log(err);
    }
  }

  const handleEditorValidation = (markers) => {
    // model markers
    markers.forEach((marker) => console.log('onValidate:', marker.message));
  }

  const getEditorValue = () => {
    return editorRef1.current?.getValue();
  }

  const handleApply = (e) => {
    e.preventDefault();
    setLoading(true);
    const currentValue = getEditorValue();
    let oprions;
    try {
      oprions = JSON.parse(currentValue);
      setOption(oprions);
      setTimeout(() => {
        setLoading(false);
      }, 2000);
    } catch (error) {
      console.log(error)
    }
  }

  const handleCompileCode = async (e) => {
    e.preventDefault();
    setLoading(true);
    const url = 'https://api.codex.jaagrav.in';
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: new URLSearchParams({
        code: editorRef.current?.getValue(),
        language: compilerLang
      })
    };
    try {
      const response = await fetch(url, options);
      const result = await response.json();
      if (result.status === 200) {
        if (result.error === "") {
          setError(false)
          setCompilerOutput("Output:- " + result.output);
        } else {
          setError(true);
          setCompilerOutput("Output:- " + result.error);
        }
      } else {
        toast.error("Something Went Wrong, Please try again!", {
          position: toast.POSITION.TOP_RIGHT
        });
      }
      setTimeout(() => {
        setLoading(false);
      }, 2000);
    } catch (error) {
      setLoading(false);
      console.error(error);
    }
  }

  const handleDownload = () => {
    let fileName = prompt("Enter file name to save:- ", "");
    let fileExt = '';
    if (fileName !== null || fileName !== "") {
      if (language === "python") {
        fileExt = "py"
      } else if (language === "javaScript") {
        fileExt = "js"
      } else if (language === "csharp") {
        fileExt = "cs"
      }

      if (fileExt === '') {
        fileName = `${fileName}.${language}`;
      } else {
        fileName = `${fileName}.${fileExt}`;
      }
    }
    let code = editorRef.current?.getValue();
    const element = document.createElement('a');
    const file = new Blob([code], { type: 'text/plain' });
    element.href = URL.createObjectURL(file);
    element.download = fileName || 'download.txt';
    document.body.appendChild(element);
    element.click();
    document.body.removeChild(element);
  };

  const setLanguageHandler = (e) => {
    try {
      e.preventDefault();
      let lang = e.target.value;
      if (lang === "js") {
        setLanguage("javascript");
      } else if (lang === "cs") {
        setLanguage("csharp");
      } else if (lang === "py") {
        setLanguage("python");
      } else if (lang === "txt") {
        setButtonDisabled(true);
      } else {
        setLanguage(lang);
      }

      setCompilerlang(lang);

    } catch (e) {
      console.log(e);
    }
  }

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 4000);
  }, []);

  return (
    <div className='editorAligment' >
      <div className='editorAligment1'>
        <ToastContainer autoClose={2000} />
        <LoadingSpinner visible={loading} />
        <Editor
          height="60vh"
          width="70vw"
          theme='vs-dark'
          defaultValue={"//Code Something Here"}
          language={language}
          onMount={handleEditorDidMount}
          loading={true}
          options={option}
          onValidate={handleEditorValidation}
          value={fileContent}
          className='clsEditor'
        />
        <div className='outputField'>
          <div className={`${error ? 'clsErrorOutput' : 'clsSuccessOutput'}`}>
            {compilerOutput}
          </div>
        </div>
      </div>
      <div className='settingForm'>
        <h1 className='settingFont'>Settings</h1>
        <hr />
        <h1 style={{ margin: "1.5vh 0vw 0.5vh 1vw", fontSize: "1.25rem" }}>Languages</h1>
        <select name="languages" className='inputField' onChange={setLanguageHandler}>
          {languageInfo?.map((option) => (
            <option key={option.name} value={option.id}>
              {option.name}
            </option>
          ))}
        </select>
        <h1 style={{ margin: "1.5vh 0vw 0.5vh 1vw", fontSize: "1.25rem" }} >Choose File</h1>
        <FileInput onFileRead={handleFileRead} />
        <h1 style={{ margin: "1.5vh 0vw 1vh 1vw", fontSize: "1.25rem" }}>Options</h1>
        <Editor
          language='json'
          height="50vh"
          width="29vw"
          theme='vs-dark'
          loading={true}
          value={JSON.stringify(option, null, 2)}
          onMount={handleEditorDidMountOption}
        />
        <div className='buttonAlignment'>
          <button className='applyBtn' onClick={handleApply}>Apply Setting</button>
          <button className='applyBtn' disabled={isButtonDisabled} onClick={handleCompileCode}>Compile Code</button>
          <button className='applyBtn' onClick={handleDownload} >Download Code</button>
        </div>
      </div>
    </div>
  )
}

export default CodeEditor
