import React, { createContext, useState } from 'react'

const NavigateContext = createContext();

const NavigateContextProvider = ({ children }) => {
    const [editor, setEditor] = useState(true);
    const [diffEditor, setDiffEditor] = useState(false)
    return (
        <NavigateContext.Provider value={{ editor, setEditor, diffEditor, setDiffEditor }}>
            {children}
        </NavigateContext.Provider>
    )
}

export { NavigateContext, NavigateContextProvider };