import React, { useContext, useState } from 'react';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { NavigateContext } from '../contexts/NavigateContext';
import { LoaderContext } from '../contexts/LoaderContext';
const Navbar = () => {
    // const [editor, setEditor] = useState(true);
    // const [diffeditor, setDiffEditor] = useState(false)
    const [title, setTitle] = useState("<CodeX Editor IDE />");
    const { editor, setEditor, diffEditor, setDiffEditor } = useContext(NavigateContext);
    // eslint-disable-next-line 
    const { loading, setLoading } = useContext(LoaderContext)
    const handleChange = (e) => {
        try {
            if (e.target.value === "diffeditor") {
                setTitle("<CodeX DiffEditor />")
                setLoading(true);
                setDiffEditor(true);
                setEditor(false);
                toast.success("Please Wait Diff Editor is Loading!", {
                    position: toast.POSITION.TOP_RIGHT,
                    // pauseOnFocusLoss: false,
                    pauseOnHover: false
                });
            } else {
                setTitle("<CodeX Editor IDE />");
                setLoading(true);
                setDiffEditor(false);
                setEditor(true);
                toast.success("Code Editor is Loading!", {
                    position: toast.POSITION.TOP_RIGHT,
                    // pauseOnFocusLoss: false,
                    pauseOnHover: false
                });
            }

        } catch (err) {
            toast.error("Something Went Wrong, Please try again!", {
                position: toast.POSITION.TOP_RIGHT
            });

        }
    }
    return (
        <nav className="navbar">
            <div className="navbar-container">
                {/* <img src={NavLogo} alt="Logo" className="navBarLogo" /> */}
                <a href="/" className="navbar-logo">
                    {title}
                </a>
                <ToastContainer autoClose={2000} />
                <ToggleButtonGroup
                    color="primary"
                    //value={alignment}
                    exclusive
                    onClick={handleChange}
                    aria-label="Platform"
                    className='toggleButton'
                    size='small'
                >
                    <ToggleButton value="editor" selected={editor}>Editor</ToggleButton>
                    <ToggleButton value="diffeditor" selected={diffEditor}>Diff Editor</ToggleButton>
                </ToggleButtonGroup>
            </div>
        </nav>
    );
};

export default Navbar;
