import React, { useContext, useEffect, useState } from 'react';
import { DiffEditor } from '@monaco-editor/react';
import LoadingSpinner from './Loader';
import { LoaderContext } from '../contexts/LoaderContext';
import { DiffLanguageSupp } from "./DiffLanguageSupp"
const DiffEditors = () => {
    const { loading, setLoading } = useContext(LoaderContext)
    const [language, setLanguage] = useState("java");
    const options = {
        originalEditable: true,
        readOnly: false,
        fontSize: 15,
        fontFamily: 'monospace',
        lineHeight: 24,
    }

    const selectLanguageHandler = (e) => {
        try {
            e.preventDefault();
            let lang = e.target.value;
            setLanguage(lang);

        } catch (e) {
            console.log(e);
        }
    }

    useEffect(() => {
        setTimeout(() => {
            setLoading(false);
        }, 3000);
    }, []);

    return (
        <div>
            <LoadingSpinner visible={loading} />
            <DiffEditor
                language={language}
                theme='vs-dark'
                height="83vh"
                width="100vw"
                loading={true}
                options={options}
            />
            <div className='clsLanguageFormsDif'>
                <h1 style={{ margin: "1.5vh 0vw 0.5vh 1vw", fontSize: "1.25rem" }}>Select Languages:-</h1>
                <select name="languages" className='inputFieldDif' onChange={selectLanguageHandler}>
                    {DiffLanguageSupp?.map((option) => (
                        <option key={option.name} value={option.id}>
                            {option.name}
                        </option>
                    ))}
                </select>
            </div>
        </div>
    )
}

export default DiffEditors
